<script>
import $ from "jquery";
import bus from "@/bus";
import users from "../students";
import archive from "../archive";
import inactive from "../inactive";
import { Table, TableColumn } from "element-ui";

export default {
  name: "inline-actions-table",
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
    ButtonRadioGroup: () => import("@/components/ButtonRadioGroup.vue"),
    letterIndex: () => import("@/tkViews/components/layout/LetterIndex"),
  },
  data() {
    return {
      letter: "",
      users,
      archive,
      inactive,
      currentPage: 1,
      currentPage2: 1,

      deleteArchive: false,

      index: -1,

      currentGroup: "active",
      studentListOptions: [
        { value: "active", label: "Active" },
        { value: "inactive", label: "Inactive" },
        { value: "archived", label: "Archived" },
      ],
    };
  },
  mounted() {
    let self = this;

    bus.$on("onActive", (data) => {
      self.onActive(data);
    });
    bus.$on("onArchive", (data) => {
      self.onArchive(data);
    });
    bus.$on("confirmDelete", (data) => {
      // if (data.active) {
      self.onDelete(data);
      // }else {
      // self.onDeleteArchive(data)
      // }
      self.confirmDelete();
    });

    if (self.users.length > 0) {
      self.letter = self.users[0].name.substring(0, 1).toUpperCase();
    }

    $("#studentList")
      .find(".el-table__body-wrapper")
      .eq(0)
      .scroll(() => {
        let $el = $("#studentList").find(".el-table__body-wrapper").eq(0);
        let scrollTop = $el.scrollTop();
        let index = parseInt(scrollTop / 80);
        self.letter = self.users[index].name.substring(0, 1).toUpperCase();
      });

    self.initClick();
  },
  watch: {
    currentGroup(newVal, oldVal) {
      setTimeout(() => {
        this.initClick();
      }, 500)
    },
  },
  methods: {
    initClick() {
      let self = this;

      let studentList = document.getElementById("studentList");
      let ul_child = studentList.getElementsByClassName("el-table__row");
      for (var i = 0; i < ul_child.length; i++) {
        let index = i;
        ul_child[i].onclick = () => {
          $(".tk-selected").removeClass("tk-selected");
          ul_child[index].classList.add("tk-selected");
          self.preview(self.users[index]);
        };
      }
    },
    indexByLetter(data) {
      let self = this;
      let letter = data.letter;
      // console.log(letter)
      let letterIndex = 0;
      for (let i = 0; i < self.users.length; i++) {
        if (self.users[i].name.substring(0, 1).toUpperCase() == letter) {
          letterIndex = i;
          break;
        }
      }
      $("#studentList")
        .find(".el-table__body-wrapper")
        .eq(0)
        .scrollTop(letterIndex * 80);
    },
    preview(data) {
      console.log(data);
      this.$emit("preview", data);
    },
    onDelete(row) {
      // alert(`You want to delete ${row.name}`);
      let self = this;
      // self.users.some((item, i) => {
      //   if (item.id === row.id) {
      //     self.index = i;
      //   }
      // });
    },
    onDeleteArchive(row) {
      // alert(`You want to delete ${row.name}`);
      let self = this;
      // self.archive.some((item, i) => {
      //   if (item.id === row.id) {
      //     self.index = i;
      //     self.deleteArchive = true;
      //   }
      // });
    },
    confirmDelete() {
      let self = this;
      // if (self.deleteArchive) {
      //   self.archive.splice(self.index, 1);
      //   self.deleteArchive = false;
      // } else {
      //   self.users.splice(self.index, 1);
      // }
      self.users.splice(self.index, 1);
      self.index = -1;
    },
    onArchive(row) {
      // alert(`You want to archive ${row.name}`);
      let self = this;
      // self.users.some((item, i) => {
      //   if (item.id === row.id) {
      //     // self.archive.push(row);
      //     // self.users.splice(i, 1);
      //     item.archive = true;
      //   }
      // });
    },
    onActive(row) {
      // alert(`You want to archive ${row.name}`);
      let self = this;
      // self.archive.some((item, i) => {
      //   if (item.id === row.id) {
      //     // self.users.push(row);
      //     // self.archive.splice(i, 1);
      //     item.archive = false;
      //     console.log(item);
      //   }
      // });
    },
  },
};
</script>
